#imgList>li {
    width: 212px;
    height: 157px;
    float: left;
    padding: 5px;
    margin: 10px;
    box-sizing: border-box;

    >img {
        width: 200px;
        height: 145px;
    }
}

.selectImg {
    border: 2px solid royalblue;
    box-shadow: 2px 2px 10px #555;
}

#modalTool span {
    margin-left: 1rem;
    color: #868686;
    cursor: pointer; 

    &:hover {
        color: #000;
    }
}
